.main {
    width: 100%;
    height: 100%;
    padding-top: 20px;
    padding-bottom: 40px;

    background-color: var(--white-color);

    @media (min-width: 768px) {
        padding-top: 60px;
        padding-bottom: 60px;
    }
}

.gallery, .myLibGallery {
    width: 280px;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;   
    margin-left: auto;
    margin-right: auto;

    @media (min-width: 768px) {
        width: 704px;
    }

    @media (min-width: 1280px) {
        width: 1217px;
    }
    
        &__item {
            width: 280px;
            height: 443px;

            @media (min-width: 768px) {
                width: 336px;
                height: 494px;
            }

            @media (min-width: 1280px) {
                width: 395px;
                height: 634px;
            }

            &:not(:first-child) {
                margin-top: 20px;

                @media (min-width: 768px) {
                    margin-top: 32px;
                }
            }

            &:nth-child(2) {
                @media (min-width: 768px) {
                    margin-top: 0;
                }
            }

            &:nth-child(2n) {
                @media (min-width: 768px) and (max-width: 1279px) {
                    margin-left: 32px;
                }
            }

            &:nth-child(3) {
                @media (min-width: 1280px) {
                    margin-top: 0;
                }
            }

            &:nth-child(3n+2) {
                @media (min-width: 1280px) {
                    margin-left: 16px;
                }
            }

            &:nth-child(3n+3) {
                @media (min-width: 1280px) {
                    margin-left: 16px;
                }
            }
        }
}

.film {    
    &__poster {
        height: 402px;

        @media (min-width: 768px) {
            height: 455px;
        }

        @media (min-width: 1280px) {
            height: 574px;
        }
    }

    &__title {
        margin-top: 10px;

        color: var(--black-color);

        @media (min-width: 768px) {
            margin-top: 8px;
        }

        @media (min-width: 1280px) {
            margin-top: 12px;

            font-size: 20px;
            line-height: 24px;
        }        
    }

    &__characteristics {
        color: var(--secondary-color);

        @media (min-width: 1280px) {
            font-size: 20px;
            line-height: 24px;
        }        
    }
}

.pagination-number,
.pagination__dotsNext, 
.pagination__lastPage,
.pagination__firstPage,
.pagination__dotsPrev {
    width: 40px;
    height: 40px;

    display: flex;
    justify-content: center;
    align-items: center;

    line-height: 16px;
    color: var(--black-color);
    border: none;
    border-radius: 5px;
    background-color: var(--white-color);
}

.pagination {
    padding-top: 40px;
    padding-bottom: 40px;
    margin-left: auto;
    margin-right: auto;

    width: 280px;
    height: 40px;
    display: flex;
    justify-content: center;

    @media (min-width: 768px) {
        padding-top: 60px;

        width: 440px;
        height: 40px;
    }
    
    &__current {
        display: flex;
    }

    &__previousPage,
    &__nextPage {  
        position: relative;
 
        width: 40px;
        height: 40px;

        display: flex;
        justify-content: center;
        align-items: center;

        line-height: 16px;
        color: var(--black-color);
        background-color: var(--light-orange-color);
        background-size: 16px;
        background-position: center;
        background-repeat: no-repeat;
        border: none;
        border-radius: 5px;
    }

    &__previousPage {
        margin-right: 3px;
        background-image: url(../images/left.svg);
    }

    &__nextPage {
        margin-left: 3px;
        background-image: url(../images/right.svg); 
    }

    &__firstPage,
    &__lastPage {
        cursor: pointer;

        &:hover, &:focus {
            color: var(--primary-color);
        }

        @media (max-width: 767px) {
            display: none;
        }
    }

    &__dotsPrev,
    &__dotsNext {
        @media (max-width: 767px) {
            display: none;
        }
    }
}

.pagination-number {
    cursor: pointer;

    &:hover, &:focus {
        color: var(--primary-color);
    }

    &.is-active {
        color: var(--white-color);
        background-color: var(--primary-color);
    }
}

.disabled {
    background-color: var(--background-footer);
}
